const time = {
  // Date  xxxx-xx-xx 
  dateToFormat:function(_data) {
    const year = _data.getFullYear();//(4,1970)

    let month = _data.getMonth()+1;//(0-11,01,1)

    if( month <= 9 ){
      month = "0"+month;
    }

    let date = _data.getDate();//(1-31)

    if( date <= 9 ){
      date = "0"+date;
    }

    return year+"-"+month+"-"+date;
  },
  // Timestamp  xxxx-xx-xx 
  stampToFormat:function(stamp) {
    let _data = new Date(stamp);

    //
    const year = _data.getFullYear();

    // (0 ~ 11)
    let month = _data.getMonth() + 1;
    if( month <= 9 ){
      month = "0"+month;
    }

    // (1 ~ 31)
    let date = _data.getDate();
    if( date <= 9 ){
      date = "0"+date;
    }

    return year+"-"+month+"-"+date;
  },
  // Date  YYYY-MM-DD HH:mm
  dateToMoment:function(_data) {

    //
    const year = _data.getFullYear();

    // (0 ~ 11)
    let month = _data.getMonth()+1;

    // (1 ~ 31)
    let date = _data.getDate();

    // (0 ~ 23)
    let hours = _data.getHours();

    // (0 ~ 59)
    let minutes = _data.getMinutes();

    return year + "-" + month + "-" + date + " " + hours + ":" + minutes;
  }
}


export default time


