import React from 'react';
import { connect } from 'react-redux';
import {Accordion, List} from 'antd-mobile';
import assign from 'lodash.assign';

import topicon from './../icon/returnback.png';
import dateTime from './../method/dateTime.jsx';

const Item = List.Item;
const Brief = Item.Brief;


class Stage2 extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      first:false,
      next:true,
      orderName:"...",
      orderSn:"...",
      payStatus:"...",
      roomNum:"...",
      peopleNum:"...",
      adultNum:"...",
      childNum:"...",
      checkIn:"...",
      checkOut:"...",
      cycleLength:"...",
      productAmount:"...",
      discount:"...",
      orderAmount:"...",
      calMethod:"...",
      payAmount:"...",
      notPayAmount:"...",
    };
  }
  componentDidMount() {
    if (this.props.infor.loaddata == null) {
      this.props.router.push('/');
      return
    }
    let _date = assign({},this.state);
    // 
    if (this.props.infor.loaddata.isRead == "N") {
      _date.first = true;
    }else {
      _date.first = false;
    }

    // 
    _date.orderName = this.props.infor.loaddata.orderName;
    // 
    _date.orderSn = this.props.infor.loaddata.orderSn;
    // 
    let payStatus = '';
    if (this.props.infor.loaddata.payStatus == 1) {
      payStatus = "";
    }else {
      payStatus = "";
    }
    _date.payStatus = payStatus;
    // 
    _date.roomNum = this.props.infor.loaddata.roomNum;
    // 
    _date.peopleNum = this.props.infor.loaddata.peopleNum;
    // 
    _date.adultNum = this.props.infor.loaddata.adultNum;
    // 
    _date.childNum = this.props.infor.loaddata.childNum;
    // 
    _date.checkIn = dateTime.stampToFormat(this.props.infor.loaddata.checkIn);
    // 
    _date.checkOut = dateTime.stampToFormat(this.props.infor.loaddata.checkOut);
    // 
    const cycleLength = Math.floor((this.props.infor.loaddata.checkOut - this.props.infor.loaddata.checkIn)/86400000);
    _date.cycleLength = (cycleLength+1)+''+cycleLength+'';
    // 
    _date.productAmount = this.props.infor.loaddata.productAmount;
    // 
    _date.discount = this.props.infor.loaddata.discount;
    // 
    _date.orderAmount = this.props.infor.loaddata.orderAmount;
    // 
    _date.calMethod = this.props.infor.loaddata.calMethod;
    // 
    _date.payAmount = this.props.infor.loaddata.payAmount;
    // 
    _date.notPayAmount = this.props.infor.loaddata.notPayAmount;

    this.setState(_date);
  }
  render() {
    return (
      <div>
        <div className="NavTOP">
          {/*<div
            onClick={function(){
              if (this.state.first == false) {
                this.props.router.push('/index');
              }else {
                this.props.router.push('/s1');
              }
            }.bind(this)}
          >
            <div style={{
              width: '0.7rem',
              height: '0.7rem',
              background: 'url('+topicon+') center center /  0.7rem 0.7rem no-repeat' }}
            />
            <span  className="tipName"></span>
          </div>*/}
          <span className="NavName"></span>
        </div>
        <div className="part2">

          <List className="my-list">
            <Item><Brief>{this.state.orderName}</Brief></Item>
            <Item extra={this.state.orderSn}></Item>
            <Item extra={this.state.payStatus}></Item>
          </List>

          <div className="interval"></div>
          <div className="Accordion">
            <Accordion className="my-accordion">
              <Accordion.Panel header="">
                <div className="List">
                  <List className="my-list">
                    <Item extra={this.state.roomNum}></Item>
                    <Item extra={this.state.peopleNum}></Item>
                    <Item extra={this.state.adultNum}></Item>
                    <Item extra={this.state.childNum}></Item>
                  </List>
                </div>
              </Accordion.Panel>
            </Accordion>
          </div>

          <div className="interval"></div>
          <div className="Accordion">
            <Accordion className="my-accordion">
              <Accordion.Panel header="">
                <div className="List">
                  <List className="my-list">
                    <Item extra={this.state.checkIn}></Item>
                    <Item extra={this.state.checkOut}></Item>
                    <Item extra={this.state.cycleLength}></Item>
                  </List>
                </div>
              </Accordion.Panel>
            </Accordion>
          </div>

          <div className="interval"></div>
          <div className="Accordion">
            <Accordion className="my-accordion">
              <Accordion.Panel header="">
                <div className="List">
                  <List className="my-list">
                    <Item extra={this.state.productAmount}></Item>
                    <Item extra={this.state.discount}></Item>
                    <Item extra={this.state.orderAmount}></Item>
                    <Item><Brief>{this.state.calMethod}</Brief></Item>
                    <Item extra={this.state.payAmount}></Item>
                    <Item extra={this.state.notPayAmount}></Item>
                  </List>
                </div>
              </Accordion.Panel>
            </Accordion>
          </div>
        </div>
        <div className="NavBottom">
          {(function(){
            let _this = this;
            if (this.state.first) {
              if (this.state.next) {
                return <div className="submitContent">
                  <div className="saveDataActive" onClick={function(){
                    if (_this.state.first == false) {
                      _this.props.router.push('/index');
                    }else {
                      _this.props.router.push('/s1');
                    }
                  }}></div>
                  <div className="newNextPageActive" onClick={function(){
                    // 
                    if (!_this.props.infor.loaddata.present) {
                      _this.props.router.push('/s4');
                      document.body.scrollTop = document.documentElement.scrollTop = 0;
                    }else {
                      _this.props.router.push('/s3');
                      document.body.scrollTop = document.documentElement.scrollTop = 0;
                    }
                  }}></div>
                </div>
              }else {
                return <div className="NextPage"></div>
              }
            }else {
              return <div className="NextPageActi" onClick={function(){
                _this.props.router.push('/index');
                document.body.scrollTop = document.documentElement.scrollTop = 0;
              }}></div>
            }
          }.bind(this))()}
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state, ownProps) => ({
  infor:state.reducer.infor
});

export default Stage2 = connect(
  mapStateToProps
)(Stage2);




